<template>
  <div class="middle-content">
    <div class="layout">
      <div class="top-section" style="height: calc(33vh - 0.3rem)">
        <SubtitleBar :subtitle="`运维统计`">
          <a-select
            v-model:value="formState.operation"
            :options="operationTypeData"
            @change="handleOperationTypeChange"
            style="width: 1.2rem"
          />
        </SubtitleBar>
        <IndicatorGroup
          class="indicator-group-layout"
          :data="omData"
          @click-item="operationToBiReport"
        />
        <div style="transform: scale(0.7)">
          <NtgaleChart
            id="Overview-Ntgale-Chart"
            :width="350"
            :height="250"
            :data="NtgaleData.pieData"
            showLabel
            :roseType="false"
            labelFormatter="{b} ({c}笔) {d}%"
            @click-item="operationToBiReport"
          />
        </div>
      </div>
      <div class="middle-section" style="height: calc(33vh - 0.3rem)">
        <SubtitleBar :subtitle="`安全运行`">
          <a-select
            v-model:value="formState.safeOperation"
            :options="safeTypeData"
            @change="handleSafeTypeChange"
            style="width: 1.2rem"
          />
        </SubtitleBar>
        <IndicatorGroup
          class="indicator-group-layout"
          :data="securityData"
          @click-item="safetyToBiReport"
        />
        <div
          style="width: 4rem; margin-top: 0.05rem; margin-left: 1.75rem; transform: scale(1.175)"
        >
          <WaterLevelChart
            :data="formState.signRage"
            :width="`1.5rem`"
            :height="`2rem`"
            :circle="{ top: '0.49rem', left: '0.24rem', width: '1.02rem', height: '1.02rem' }"
            @click-item="safetyToBiReport"
          />
        </div>
      </div>
      <div class="bottom-section" style="height: calc(33vh - 0.3rem)">
        <SubtitleBar :subtitle="`运维人员`">
          <a-select
            v-model:value="formState.selectType"
            :options="selectTypeData"
            @change="handleTypeChange"
            style="width: 1.2rem"
          />
        </SubtitleBar>
        <div style="width: 100%; margin-top: -0.1rem">
          <DonutChart
            :title="donutTitle"
            :data="donutData.pieData"
            width="5.12rem"
            height="2.25rem"
            radius="100"
            @click-item="personToBiReport"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts" setup>
  import { ref, reactive, watch, UnwrapRef } from 'vue';
  import DonutChart from '/@/components/Framework/Chart/DonutChart.vue';
  import NtgaleChart from '/@/components/Framework/Chart/NtgaleChart.vue';
  import SubtitleBar from '/@/components/Framework/Chart/SubtitleBar.vue';
  import IndicatorGroup from '/@/components/Framework/Chart/IndicatorGroup.vue';
  import WaterLevelChart from '/@/components/Framework/Chart/WaterLevelChart.vue';
  import { assign } from 'lodash-es';
  import * as OverviewApi from '@/api/da/cockpit/overview';
  import { addTabPage } from '@/utils/route';
  import { handleRouteChange } from '../../utils';

  //搜索参数
  const formState = reactive({
    selectType: 'ageRange',
    safeOperation: 'responsibility',
    signRage: 0,
    operation: 'workOrder',
  });
  const centerSearchInfo: UnwrapRef<any> = reactive({});

  const props = defineProps({
    netIncome: { type: String, default: '' },
    netIncomeQoq: { type: String, default: '' },
    netIncomeYoy: { type: String, default: '' },
    searchInfoByTime: { type: Object, default: '' },
  });

  watch(
    () => props.searchInfoByTime,
    (value) => {
      assign(centerSearchInfo, value);
      doOperationPerson(formState.selectType);
      doSafeResp(formState.safeOperation);
      doWorkOrder(formState.operation);
    },
  );

  // ==================================运维统计 begin ======================================
  const operationTypeData: any = ref([
    { label: '工单', value: 'workOrder' },
    { label: '巡检', value: 'inspection' },
    { label: '两票', value: 'twoVotes' },
  ]);
  const omData = ref({});
  const NtgaleData = ref({ pieData: [] });
  const handleOperationTypeChange = (value) => {
    doWorkOrder(value);
  };
  const doWorkOrder = async (value) => {
    // NtgaleData.value.splice(0, NtgaleData.value.length);
    if (value === 'workOrder') {
      const workOrder = await OverviewApi.workOrder({
        startMonth: centerSearchInfo.startMonth,
        endMonth: centerSearchInfo.endMonth,
      });
      if (workOrder) {
        omData.value = [
          { label: '工单总数', value: workOrder.total || 0 },
          { label: '未闭环工单数', value: workOrder.dealTotal || 0 },
          { label: '工单闭环率', value: workOrder.dealRate || 0 },
        ];
        if (workOrder.pieData) {
          NtgaleData.value.pieData = workOrder.pieData;
        }
      }
    } else if (value === 'inspection') {
      const inspection = await OverviewApi.inspection({
        startMonth: centerSearchInfo.startMonth,
        endMonth: centerSearchInfo.endMonth,
      });
      if (inspection) {
        omData.value = [
          { label: '未巡检数', value: inspection.unInspectionCount || 0 },
          { label: '巡检异常项', value: inspection.waitCount || 0 },
          { label: '巡检完成率', value: inspection.inspectionRate || 0 },
        ];
        if (inspection.pieData) {
          NtgaleData.value.pieData = inspection.pieData;
        }
      }
    } else if (value === 'twoVotes') {
      const twoVotes = await OverviewApi.twoTicket({
        startMonth: centerSearchInfo.startMonth,
        endMonth: centerSearchInfo.endMonth,
      });
      if (twoVotes) {
        omData.value = [
          { label: '电气一票数', value: twoVotes.firstCount || 0 },
          { label: '电气二票数', value: twoVotes.secondCount || 0 },
          { label: '操作票数', value: twoVotes.operateCount || 0 },
        ];
        if (twoVotes.pieData) {
          NtgaleData.value.pieData = twoVotes.pieData;
        }
      }
    }
  };

  // ==================================安全运行 begin ======================================
  const safeTypeData: any = ref([
    { label: '安全责任书', value: 'responsibility' },
    { label: '安全检查', value: 'check' },
    { label: '安全工器具', value: 'tools' },
  ]);
  const securityData = ref({});
  const handleSafeTypeChange = (value) => {
    doSafeResp(value);
  };
  const doSafeResp = async (value) => {
    if (value === 'responsibility') {
      const res = await OverviewApi.safeResp({
        startMonth: centerSearchInfo.startMonth,
        endMonth: centerSearchInfo.endMonth,
      });
      if (res) {
        securityData.value = [
          { label: '应签订(人)', value: res.supposedSignNum || 0 },
          { label: '已签订(人)', value: res.signNum || 0 },
          { label: '签订率(%)', value: res.signRage || 0 },
        ];
        formState.signRage = res.signRage;
      }
    } else if (value === 'check') {
      const res = await OverviewApi.safeCheck({
        startMonth: centerSearchInfo.startMonth,
        endMonth: centerSearchInfo.endMonth,
      });
      if (res) {
        securityData.value = [
          { label: '检查次数(次)', value: res.checkCount || 0 },
          { label: '发现隐患(项)', value: res.hiddenCount || 0 },
          { label: '整改率', value: res.dealRate || 0 },
        ];
        formState.signRage = res.dealRate;
      }
    } else if (value === 'tools') {
      const res = await OverviewApi.safeTools({
        startMonth: centerSearchInfo.startMonth,
        endMonth: centerSearchInfo.endMonth,
      });
      if (res) {
        securityData.value = [
          { value: res.withinNum || 0, label: '待检数(个)' },
          { value: res.unInspectedNum || 0, label: '超期未检数(个)' },
          { lvalue: res.qualifiedRate || 0, label: '合格率(%)' },
        ];
        formState.signRage = res.qualifiedRate;
      }
    }
  };

  // ==================================运维人员 begin ======================================
  const donutTitle = ref('总人数');
  const selectTypeData: any = ref([
    { label: '年龄结构', value: 'ageRange' },
    { label: '学历结构', value: 'degree' },
  ]);

  const donutData = ref({ pieData: [] });
  const handleTypeChange = (value) => {
    doOperationPerson(value);
  };
  const doOperationPerson = async (value) => {
    const data = await OverviewApi.operationPerson({
      startMonth: centerSearchInfo.startMonth,
      endMonth: centerSearchInfo.endMonth,
    });
    if (data) {
      if (value == 'ageRange') {
        donutData.value.pieData = data.ageRange;
      } else {
        donutData.value.pieData = data.degree;
      }
    }
  };

  //==================================穿透方法======================================
  const operationToBiReport = () => {
    if (formState.operation === 'twoVotes') {
      //两票
      handleRouteChange('/bi/maintenance/ticket', {});
    } else if (formState.operation === 'workOrder') {
      //工单
      handleRouteChange('/bi/maintenance/workorder', {});
    } else if (formState.operation === 'inspection') {
      //巡检
      handleRouteChange('/bi/inspection/index', {});
    }
  };

  const safetyToBiReport = () => {
    if (formState.safeOperation === 'responsibility') {
      //安全责任书
      addTabPage('/bi/safety/agreement');
    } else if (formState.safeOperation === 'check') {
      //安全检查
      handleRouteChange('/bi/safety/check', {
        dateCycle: 'month',
      });
    } else if (formState.safeOperation === 'tools') {
      //安全工器具
      addTabPage('/bi/safetytools/index');
    }
  };

  const personToBiReport = () => {
    if (formState.selectType === 'ageRange') {
      //年龄
      addTabPage('/bi/integrated/personnelStructure');
    }
  };
  // onMounted(async () => {
  //   doOperationPerson(formState.selectType);
  //   doSafeResp(formState.safeOperation);
  //   doWorkOrder(formState.operation);
  // });
</script>

