  .middle-content {
    flex: 28;
    flex-shrink: 0;
    width: 28%;
    padding-right: 0.46rem;

    .layout {
      display: flex;
      flex-direction: column;
      flex-shrink: 0;
      height: 100%;

      .top-section {
        flex: 10;
        height: 33%;
      }

      .middle-section {
        flex: 10;
        height: 33%;
      }

      .bottom-section {
        flex: 10;
        height: 33%;
